import React from "react";
import "./App.css";
import CrossfadeImage from "./CrossfadeImage";

import images from "./images"

import logo from "./images/cnz_logo_png_white.png";

function App() {
  return (
    <div className="App">
      <div id="container">

        <div className="row">
          <div className="box">
            <CrossfadeImage src={images[0]}/>
          </div>
          <div className="box">
          <CrossfadeImage src={images[1]}/>
          </div>
          <div className="box">
          <CrossfadeImage src={images[2]}/>
          </div>
          <div className="box">
          <CrossfadeImage src={images[3]}/>
          </div>
        </div>
        <div className="row">
          <div className="box">
          <CrossfadeImage src={images[4]}/>
          </div>
          <div className="box">
          <CrossfadeImage src={images[5]}/>
          </div>
          <div className="box">
          <CrossfadeImage src={images[6]}/>
          </div>
          <div className="box">
          <CrossfadeImage src={images[7]}/>
          </div>
        </div>
        <div className="row">
          <div className="box">
          <CrossfadeImage src={images[8]}/>
          </div>
          <div className="box">
          <CrossfadeImage src={images[9]}/>
          </div>
          <div className="box">
          <CrossfadeImage src={images[10]}/>
          </div>
          <div className="box">
          <CrossfadeImage src={images[11]}/>
          </div>
        </div>
        <div className="row">
          <div className="box">
          <CrossfadeImage src={images[12]}/>
          </div>
          <div className="box">
          <CrossfadeImage src={images[13]}/>
          </div>
          <div className="box">
          <CrossfadeImage src={images[14]}/>
          </div>
          <div className="box">
          <CrossfadeImage src={images[0]}/>
          </div>
        </div>

      </div>
      <img className="logo" src={logo} />
    </div>
  );
}

export default App;
